<template>
      <div class="choose-template">
        <router-link class="back-btn" to="/first"><a-icon type="arrow-left" /> Back</router-link> 
        <h2 class="step-title">Please choose a template</h2>
          <div class="choose-wrap">
                <a-row :gutter="30">
                    <a-col :md="8" :sm="12" :xs="24"><h4 class="text-center">Blank Template</h4>
                        <div @click="getTemplate('blank-template')" class="empty"></div>
                    </a-col>
                     <a-col :md="8" :sm="12" :xs="24"><h4 class="text-center">Template Two</h4>
                        <img @click="getTemplate('template-two')" class="template-img" src="https://picsum.photos/201/201" alt="">
                    </a-col>
                     <a-col :md="8" :sm="12" :xs="24"><h4 class="text-center">Template Three</h4>
                        <img @click="getTemplate('template-three')" class="template-img" src="https://picsum.photos/199/201" alt="">
                    </a-col>
                </a-row>
          </div>
    </div>
</template>

<script>
import {store} from './../store'
import {userdb,auth} from './../firebase'
export default {
  name:'ChooseTemplate',
data(){
       return{
           template:'',
       }
   },
   methods:{
     getTemplate(name){
         let self = this;
         if(!name){
             this.$message.error('Please choose at least one template', 2);
             return;
         }
         self.template = name;
         store.commit('addToProject',{template:self.template});
            var currentProject = store.getters.getProject;
            userdb.doc(auth.currentUser.uid).collection('projects').add(currentProject).then((data)=>{
               localStorage.clear();
               localStorage.setItem('gjs-doc-id',data.id);
               localStorage.setItem('gjs-project-title',currentProject.projectTitle)
               localStorage.setItem('gjs-project-desc',currentProject.projectDesc)
                self.$router.push('/builder/'+data.id);
             })
        }
    }
}
</script>

<style scoped>
.back-btn{
    position:fixed; 
    top:30px;
    left:40px;
}
.back-btn:hover{
    cursor:pointer;
}
.choose-template{
    width:100vw;
    height:100vh;
    overflow:hidden;
}
.choose-wrap{
    width:80%;
    margin:auto;
    height:80%;
    overflow-y:scroll;
    overflow-x:hidden;
}
.choose-wrap .template-img{
    transition:all ease 0.5s;
    width:100%;
    height:320px;
    object-fit: cover;
    margin-bottom:30px;
    border:10px solid transparent;
}
.choose-wrap .empty{
    border:10px dashed #eee;
    text-align:center;
    width:100%;
    height:320px;
    line-height:320px;
}
.choose-wrap .template-img:hover,.choose-wrap .empty:hover{
    cursor:pointer;
    border:10px solid #44cfa9;
}
@media (max-width:576px){
    .choose-wrap{
        width:100%;
    }
}
</style>
